import React from 'react';
import Nav from './Nav';
import {Route, Link} from 'react-router-dom';
import Home from './Home';
import EmptyCard from './EmptyCard';
import List from './List';
import EditList from './EditList';

const ContainerHome = () => {
    return (
        <div>
            <Nav/>
            <Route path="/home" exact component={Home}></Route>
            <Route path="/home/setting" exact component={Settings}></Route>
            <Route path="/home/add" component={Add}></Route>
            <Route path="/home/list" exact component={List}></Route>
            <Route path="/home/setting/account" component={AccountSetting}></Route>
            <Route path="/home/setting/application" component={ApplicationSetting}></Route>
            <Route path="/home/list/:id" component={EditList}></Route>
        </div>
    )
};

export default ContainerHome;
const Settings = () => {
    return (
      <div className="Settings-container">
        <div className="myQuizzes-container">
        <EmptyCard>
          <p><Link to="/home/setting/account">Account</Link></p>
        </EmptyCard>
        </div>
        <div className="createNew-container">
        <EmptyCard>
          <p><Link to="/home/setting/application">Application</Link></p>
        </EmptyCard>
        </div>
      </div>
    )
  }
  
  const Add = () => {
    return (
      <div className="Quizzes-container">
        <p>Add here</p>
      </div>
    )
  }
  
  
  const AccountSetting = () => {
    return (
      <div className="AccountSetting-container">
        <p>Username</p>
        <p>Password</p>
      </div>
    )
  }
  
  const ApplicationSetting = () => {
    return (
      <div className="ApplicationSetting-container">
        <p>Theme</p>
        <p>Stats</p>
      </div>
    )
  }