import React from 'react';
import {Link} from 'react-router-dom';
import '../styles/nav.css';

export default function NavSlider(props) {

    return (
        <div className = {props.navPos}>
            <div className = "flex-center">
                <nav>
                    <ul>
                        <li onClick = {props.toggle}><Link to="/home">Home</Link></li>
                        <li onClick = {props.toggle }><Link to="/home/setting">Settings</Link></li>
                    </ul>
                </nav>
            </div>
        </div>
    )
};
