import React, {useState, useEffect} from 'react';
import Menu from '../assets/List.png'
import NavSlider from './NavSlider'
import '../styles/nav.css'

const Nav = () => {

const [isActive, setIsActive] = useState(false);
const [navPos, setNavPos] = useState("nav off");
const [laySwitch, setLaySwitch] = useState("overlay layOff");

useEffect( () => {
  const newPos = isActive ? "nav on" : "nav off";
  const newLay = isActive ? "overlay layOn" : "overlay layOff";
  setNavPos(newPos);
  setLaySwitch(newLay);
}, [isActive]);

const toggleClick = () => {
    setIsActive(!isActive);
  }
const toggleOverlay = () => {
  isActive ? toggleClick() : console.log("nav is closed");
}


  return (
    <div className="Nav-container">
        <img className="Img" src={Menu} width= "20px" onClick={toggleClick}/>
        <div className={laySwitch} onClick={toggleOverlay}/>
        <NavSlider navPos = {navPos} toggle = {toggleClick}/>
    </div>
  );
}

export default Nav;