import React from 'react'
import {Link} from "react-router-dom";
import '../styles/containers.css';
import EmptyCard from '../components/EmptyCard.js'

const Login = () => {
    return (
      <div>
      <EmptyCard>
        <div>
          <EmptyCard>
            <p>Ar</p>
          </EmptyCard>
          <EmptyCard>
            <p>ActiveRecall</p>
          </EmptyCard>
        </div>
      </EmptyCard>
      <EmptyCard>
        <input className="block" placeholder='enter email'></input>
      </EmptyCard>
      <EmptyCard>
        <input className="block" placeholder='enter password'></input>
      </EmptyCard>
      <EmptyCard>
        <button className="block" ><Link to='/home'>Login</Link></button>
      </EmptyCard>  
      </div>
    )
  }

export default Login;