import React, { useState } from 'react'
import '../styles/containers.css';
import {useHistory, Route, Switch} from 'react-router-dom';
import files from '../server/files.json'
import ListComp from './ListComp'

export default function EditList({match,location,props}) {
    const [details,setDetails] = useState(files);  
    const history = useHistory();
    const listView = () => {
        return(
            details[match.params.id].questions.map((file) => (
                <ListComp key={file.id.toString()} file= {file.question} url={`${match.url}/${file.id.toString()}`}/>
            ))
        )
    }
    const ansView = ({match}) => {
        // console.log(details[match.params.num].questions[match.params.id])
        return(
            <div className="flex-centre">
                <p>{details[match.params.num].questions[match.params.num].question}</p>
                <p>{details[match.params.num].questions[match.params.num].answer}</p>
            </div>
        )
    }
    return (
        <div>
            <div>
                <p>{details[match.params.id].text}</p>
            </div>
            <div className='overflow-y'> 
            <Switch>
                <Route path="/home/list/:id" exact component={listView}></Route> 
                <Route path="/home/list/:id/:num" component={ansView}></Route>
            </Switch>
            </div>
            <div className="flex-centre">
                <button onClick={() => {history.goBack()}}>back</button>
            </div>
        </div>
    )
}
