import React from 'react';
import {Link} from "react-router-dom";
import EmptyCard from './EmptyCard';

const Home = () => {
    return (
      <div className="Home-container">
        <div className="myQuizzes-container">
          <EmptyCard>
            <p><Link to="/Home/list">myQuizzes</Link></p>
          </EmptyCard>
        </div>
        <div className="createNew-container">
          <EmptyCard>
            <p><Link to="/Home/add">createNew</Link></p>
          </EmptyCard>
        </div>
      </div>
    )
  }

export default Home;